import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
} from 'recompose'
import get from 'lodash/get'

import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import { TFoot as TFootComponent } from 'features/Table'

const TfootComposed = compose(
  inject(
    'selectedMatches',
    'matches',
    'shootsRange',
    'lexics',
    'videoLink',
    'teamTable',
    'playerTable',
  ),
  //@ts-ignore
  withHandlers({
    /**
    *        
    * @param {number} param_id - id 
    * @param {string} lexical_formula -  
    * @param {array} tableStats -   
    * @param {number} id - id   
    * @param {boolean} forOpp -   .        
    */
    getExtLink: ({
      lexics: { lang, page },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => (
      {
        //@ts-ignore
        lexical_formula,
        //@ts-ignore
        param_id,
      },
      //@ts-ignore
      id,
      //@ts-ignore
      forOpp,
    ) => {
      //@ts-ignore
      const checkId = (teamId) => {
        if (page === 'player') {
          return true
        }
        if (forOpp) {
          return teamId !== id
        }
        return teamId === id
      }

      //    
      const events = tableStats.filter(
        //@ts-ignore
        ({ f_param, f_team }) => (
          f_param === param_id && checkId(f_team)),
      )
      const filteredEvents = events.map(
        (
          { f_match_event_ids_json }
          //@ts-ignore
          : { f_match_event_ids_json: Array<number | null> },
        ) => f_match_event_ids_json,
      )
        .flat()
      //   null   
        .filter(Boolean)

      if (events.length) {
        request({
          events: filteredEvents,
          lang,
          lexical_formula,
          range: shootsRange,
          type: 'tablesFooter',
        })
      }
    },
    /**
    *        
    * @param {string} type -   
    */
    setType: ({
      lexics: { page },
      playerTable,
      teamTable,
    }) => (type: string, position: number) => {
      if (page === 'team') {
        return teamTable.changeValueType(type, position)
      }
      return playerTable.changeValueType(type, position)
    },
    /**
    *          .
         
    * @param {string} page -   
    * @param {string} pageId - id /
    * @param {array} tableStats -  
    */
    getActualMatchesList: ({
      lexics: { page },
      selectedMatches,
      tableStats,
    }) => ({
      forOpps,
      pageId,
    }: {
      pageId: string,
      forOpps: boolean,
    }) => {
      const selectedMatchesList: Array<number> = get(selectedMatches, `${page}.${pageId}`, [])
      //   
      if (forOpps) {
        //@ts-ignore
        return tableStats.filter(({ f_match, f_team }) => (
          f_team !== +pageId && selectedMatchesList.includes(f_match)))
      }
      //          
      // (     )
      if (page === 'player') {
        //@ts-ignore

        return tableStats.filter(({ f_match }) => (
          selectedMatchesList.includes(f_match)))
      }
      //   
      //@ts-ignore
      return tableStats.filter(({ f_match, f_team }) => (
        f_team === +pageId && selectedMatchesList.includes(f_match)))
    },
  }),
  clickableClassNameHOC,
  observer,
)

export const TFoot = TfootComposed(TFootComponent)
