import { types } from 'mobx-state-tree'
import isEqual from 'lodash/isEqual'
import isEmpty from 'lodash/isEmpty'
import reduce from 'lodash/reduce'
import uniq from 'lodash/uniq'

import stores from 'features/stores'

const SelectedMatchesActions = types.model({}).actions(self => ({
  //     
  //@ts-ignore
  setSelected(selected: Array<number>, pageId: string) {
    //@ts-ignore
    const { page } = stores.lexics
    //@ts-ignore
    self[page] = {
      [pageId]: selected,
    }
  },

  //       (   )
  setTempSelected(selected: Array<number>) {
    //@ts-ignore
    self.tempSelected = selected
  },

  //    (  ,   )
  //@ts-ignore
  setPeriod(period?: (string | number)) {
    //@ts-ignore
    self.selectedPeriod = period
  },

  //   / 
  //@ts-ignore
  setOpened(pageId) {
    //@ts-ignore
    const { page } = stores.lexics
    //@ts-ignore
    self.toggleSelect(!self.opened)

    //@ts-ignore
    if (self.opened) {
      //         
      //@ts-ignore
      self.setTempSelected(self[page][pageId])
    } else {
      //       API      
      //    
      //@ts-ignore
      self.onCloseActions(page, pageId)
      //@ts-ignore
      self.setSelected([...self.tempSelected], pageId)
    }
  },
  //@ts-ignore
  toggleSelect(select) {
    //@ts-ignore
    self.opened = select
  },

  onCloseActions(page: string, pageId: string) {
    // ,     .
    const matchesListDidUpdate = (page: {}) => (
      //@ts-ignore
      !isEqual(self.tempSelected, page[pageId])
    )
    //@ts-ignore
    const { profile } = stores.playerProfile
    //@ts-ignore
    const { player, team } = stores.selectedMatches
    const isPlayerPage = page === 'player'
    //@ts-ignore
    const { fetch: fetchMarkers } = stores.markers
    //@ts-ignore
    if (isEmpty(self.tempSelected)) return self.eraseAllData(isPlayerPage)

    if (isPlayerPage && matchesListDidUpdate(player)) {
      //@ts-ignore
      const { fetch: fetchHeaderStats } = stores.playerStats
      //@ts-ignore
      const { fetchTableStats } = stores.playerTable
      //       
      //@ts-ignore
      fetchTableStats(self.tempSelected, pageId)
      //     
      //@ts-ignore
      fetchHeaderStats(pageId, self.tempSelected, profile.amplua.id)
      //   
      //@ts-ignore
      fetchMarkers(pageId, self.tempSelected)
    }

    if (!isPlayerPage && matchesListDidUpdate(team)) {
      //@ts-ignore
      const { fetch: fetchHeaderStats } = stores.teamStats
      //@ts-ignore
      const { list } = stores.matches
      //@ts-ignore
      const { fetchTableStats } = stores.teamTable
      //@ts-ignore
      const { fetchParams } = stores.teamPlayers
      const teamIds = reduce(list[pageId].matches,
        (ids, { f_match, f_team_opponent }) => {
          //@ts-ignore
          if (self.tempSelected.includes(f_match)) {
            //@ts-ignore
            ids.push(f_team_opponent)
          }
          //      
          //@ts-ignore
          ids.push(+pageId)
          return uniq(ids)
        }, [])

      //     
      //@ts-ignore
      fetchHeaderStats(pageId, self.tempSelected)
      //       
      //@ts-ignore
      fetchTableStats(self.tempSelected, teamIds)
      //    
      //@ts-ignore
      fetchParams(+pageId, stores.selectedMatches)
      //   
      //@ts-ignore
      fetchMarkers(pageId, self.tempSelected)
    }
  },

  //   0 ,      
  eraseAllData(isPlayerPage: boolean) {
    const { eraseHeaderStats } = isPlayerPage
      //@ts-ignore
      ? stores.playerStats
      //@ts-ignore
      : stores.teamStats
    eraseHeaderStats()
    //@ts-ignore
    stores.markers.deleteMarkers()
  },
}))

export default SelectedMatchesActions
