import { types } from 'mobx-state-tree'
import { callApi } from 'react-sport'

import routes from 'config/routes'
import setTitle from './setTitle'

const ExternalVideoLinksActions = types.model({}).actions(self => ({
  /**        
   * @param {Array} events -    
   * @param {Object} range -         
   * @param {string} lang - 
   * @param {number || null} matchId -  ,   
   * @param {string} lexical_formula -  
   * @param {number || null} matchesCount -   
   * @param {object} matchInfo -   .    
   * @param {string} playerInfo -   ,  .
   * @param {string} tab -     
   * @param {string} type -  ,   title (  )
   * @param {string} widgetType -  ,  
   */
  request({
    //@ts-ignore
    events,
    //@ts-ignore
    lang,
    lexical_formula = '',
    matchesCount = null,
    matchId = null,
    //@ts-ignore
    matchInfo,
    //@ts-ignore
    playerInfo,
    //@ts-ignore
    range,
    tab = '',
    type = '',
    widgetType = '',
  }) {
    const title = setTitle({
      lexical_formula,
      //@ts-ignore
      matchesCount,
      matchInfo,
      tab,
      lang,
      playerInfo,
      type,
      widgetType,
    })
    const url: string = `${routes.API_ROOT}/video`
    const body: {
      allRange: boolean,
      //@ts-ignore
      events: Array<string | number | null>,
      lang: string,
      //@ts-ignore
      matchId: number | null,
      //@ts-ignore
      offset_start: number | null,
      //@ts-ignore
      offset_end: number | null,
    } = {
      allRange: false,
      events,
      lang,
      matchId,
      offset_start: range.before,
      offset_end: range.after,
      ...title,
    }

    return callApi({
      url,
      config: {
        method: 'POST',
        body: JSON.stringify(body),
      },
      //@ts-ignore
      onRequest: () => self._onRequest(),
      //@ts-ignore
      onSuccess: ({ link }) => self._onSuccess(link),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },

  _onRequest() {
    //@ts-ignore
    self.isFetching = true
  },
  //@ts-ignore
  _onSuccess(link) {
    if (link) {
      window.open(link, '_blank')
    }
    //@ts-ignore
    self.isFetching = false
  },
  //@ts-ignore
  _onError(error) {
    //@ts-ignore
    self.error = error
    //@ts-ignore
    self.isFetching = false
  },
}))

export default ExternalVideoLinksActions
