// @ts-ignore
import { action } from 'mobx'
import isPlainObject from 'lodash/isPlainObject'
import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'
import uniq from 'lodash/uniq'
import pickBy from 'lodash/pickBy'
import some from 'lodash/some'
import filter from 'lodash/filter'

import { groupFilters } from 'module/utils/helpers'

import { fetchProps } from 'config/markers'

import {
  TCheckMarkerArgs,
  TGetMarkersArgs,
  TFilter,
} from './types'
import { doubleKeyFiltration, singleKeyFiltration } from './KeysFiltration'

//TODO   
/**
 * checkMarker -     
 * @property {object} filter - 
 * @property {object} prop -  ,     
 * @property {number} player_id - id ,      4  
 * @property {array} c_match_event_prop -   ,    
 * @property {array} c_match_event_prop2 -  ,  ,
 *      
 * @property {array} c_match_event_prop_value -     
 * @property {array} c_match_event_prop_value2 -     
 *
 *   bool
 */

const checkMarker = ({
  c_match_event_prop,
  c_match_event_prop_value,
  c_match_event_prop_value2,
  c_match_event_prop2,
  f_player,
  filter,
  filterId,
  player_id,
  prop,
  store,
}: TCheckMarkerArgs) => {
  if (!isPlainObject(filter)) return null
  if (c_match_event_prop && c_match_event_prop2) {
    return doubleKeyFiltration({
      c_match_event_prop,
      c_match_event_prop_value,
      c_match_event_prop_value2,
      c_match_event_prop2,
      filterId,
      prop,
    })
  }
  if (c_match_event_prop) {
    return singleKeyFiltration({
      c_match_event_prop,
      c_match_event_prop_value,
      f_player,
      filterId,
      prop,
    })
  }
  if (filter.fetchProps === 'mapShoot') {
    const mapShoot = get(fetchProps, [store('tabs').active, 'mapShoot'])
    const mapForCheck = prop.filter(property => mapShoot.includes(property[0]))
    return (
      mapForCheck && mapForCheck.some(property => property[2] === player_id)
    )
  }
  if (
    store('lexics').page === 'player'
    || store('tabs').active === 'block'
    || (store('tabs').active === 'attack' && filter.fetchProps === 'playerInfo')
  ) {
    const playerInfo = get(fetchProps, [store('tabs').active, 'playerInfo'])
    const mapForCheck = prop.filter(property =>
      playerInfo.includes(property[0]))
    return (
      mapForCheck && mapForCheck.some(property => property[2] === player_id)
    )
  }
  return player_id === f_player
}

/**
 *   :
 *     ,     ,
 *           ,
 *      ,        .
 *            ,
 *       ,
 *      ,    
 */
const getMarkers = ({
  checkedFilters,
  markers,
  store,
}: TGetMarkersArgs) => {
  const groupedFilters: {
    [key: string]: Array<{}>,
  } = groupFilters(checkedFilters)
  const markersFilteredByGroup: {[key: string]: {}} = {}

  /**
   *         , 
   *    .
   *      ,     
   *       .
   */
  Object.entries(groupedFilters).forEach(([groupName, filters]) => {
    markersFilteredByGroup[groupName] = filter(markers, ({ f_player, prop }) =>
      some(filters, (filter: TFilter) => {
        const {
          c_match_event_prop,
          c_match_event_prop_value,
          c_match_event_prop_value2,
          c_match_event_prop2,
          id: filterId,
          player_id,
        }: TFilter = filter

        return checkMarker({
          c_match_event_prop,
          c_match_event_prop_value,
          c_match_event_prop_value2,
          c_match_event_prop2,
          f_player,
          filter,
          filterId,
          player_id,
          prop,
          store,
        })
      }))
  })

  const markersToArray = Object.entries(markersFilteredByGroup)

  /**
   *    2,      ,  
   *   
   */
  if (markersToArray.length < 2) {
    return !isEmpty(markersFilteredByGroup) ? markersToArray[0][1] : []
  }

  /**
   *       .
   */
  let result: Array<number> = []
  markersToArray.forEach(([markersGroup, markers]) => {
    /**
     *  ,   
     */
    const neededFilters = pickBy(
      groupedFilters,
      (group: Array<{}>) => ('c_match_event_prop' in group[0]
        //@ts-ignore
        ? !group[0].c_match_event_prop.includes(parseInt(markersGroup, 10))
        //@ts-ignore
        : group[0].groupName !== markersGroup),
    )
    /**
     *    ,      
     */
    const linkedMarkers = filter(markers, ({ f_player, prop }) =>
      Object.values(neededFilters).every(filters =>
      //@ts-ignore
        some(filters, (filter: TFilter) => {
          const {
            c_match_event_prop,
            c_match_event_prop_value,
            c_match_event_prop_value2,
            c_match_event_prop2,
            id: filterId,
            player_id,
          }: TFilter = filter

          return checkMarker({
            c_match_event_prop,
            c_match_event_prop_value,
            c_match_event_prop_value2,
            c_match_event_prop2,
            f_player,
            filter,
            filterId,
            player_id,
            prop,
            store,
          })
        })))

    result = [...result, ...linkedMarkers]
  })
  //  ,       
  //          
  return uniq(result)
}

const MarkersFiltration = (superclass: any) =>
  class extends superclass {
    //@ts-ignore
    @action getMarkers = (props: TGetMarkersArgs) => getMarkers(props);
  }

export default MarkersFiltration
