import { withStateHandlers } from 'recompose'
import isEmpty from 'lodash/isEmpty'
/**
 *     
 * @param {number} param - id 
 * @param {boolean} sortInc -   ( )
 */
export const setSortedIdHOC = withStateHandlers(
  //@ts-ignore
  //@ts-ignore
  ({ param = {}, sortInc = true }) => ({
    param,
    sortInc,
  }),
  {
    setSortedId: ({ param, sortInc }) => (newParam) => {
      const clickOnNewParam = param.param_id !== newParam.param_id
      const sortIsNeedReset = Boolean(!clickOnNewParam && sortInc)
      const clickCases = {
        newParamClicked: {
          param: newParam,
          sortInc: false,
        },
        simpleClick: {
          param: newParam,
          sortInc: !sortInc,
        },
        default: {
          param: {},
          sortInc: true,
        },
      }
      //           
      if (!isEmpty(param) && clickOnNewParam) return clickCases.newParamClicked
      //          ,  
      if (sortIsNeedReset) return clickCases.default

      return clickCases.simpleClick
    },
  },
)
