
import { inject, observer } from 'mobx-react'

import { compose, withHandlers, lifecycle } from 'recompose'

const TeamsComparePupupComposed = compose(
  inject(
    'matchProfile',
    'teamProfile',
    'matchTable',
    'lexics',
    'teamsCompareWidget',
    'gearPopupStore',
  ),
  //@ts-ignore
  withHandlers({
    /**
     *        
     * @param {func} saveTempParams -   
     * @param {func} handlePopupAction -   
     * @param {string} lang -  
     * @param {string} matchId - id 
     */
    onApply: ({
      gearPopupStore: { tempSelected },
      handlePopupAction,
      lexics: { lang },
      matchProfile: { matchId },
      teamsCompareWidget: { saveTempParams },
    }) => () => {
      saveTempParams(lang, matchId, tempSelected)
      handlePopupAction()
    },
    /**
     *   
     * @param {func} editTempSelected -    
     * @param {func} handlePopupAction -   
     *
     */
    onClose: ({
      gearPopupStore: { eraseTempSelected },
      handlePopupAction,
    }) => () => {
      eraseTempSelected()
      handlePopupAction()
    },
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        gearPopupStore: { fillTempSelected },
        //@ts-ignore
        teamsCompareWidget: { selectedParams },
      } = this.props
      fillTempSelected(selectedParams)
    },
  }),
  observer,
)

export default TeamsComparePupupComposed
