import { types } from 'mobx-state-tree'

import get from 'lodash/get'

export const changeValueType = types.model({}).actions(self => ({
  /**     (      )
   * @param {string} type -  
   */
  changeValueType(type: string, position: number) {
    //    ,    
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    if (get(self.tables, type === [position, 'summaryType'])) return null
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        summaryType: type,
      },
    }
  },
}))
