// @ts-ignore
export const directionBtns = (tab) => {
  switch (tab) {
    case 'attack_reception':
    case 'serve_reception':
      return { from: true, to: false }
    default:
      return { from: true, to: true }
  }
}

export const defaultDirections = {
  attack: { from: false, to: true },
  attack_reception: { from: true, to: false },
  block: { from: true, to: false },
  pass: { from: false, to: true },
  serve: { from: false, to: true },
  serve_reception: { from: true, to: false },
}

