import { types } from 'mobx-state-tree'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const saveTempParams = types.model({}).actions(self => ({
  /**
   *   ()   
   * @param {number} position -   
   * @param {string} localStorageName -     localStorage
   * @param {array} selectedParams -  ,     
   */
  saveTempParams({
    ampluaId,
    localStorageName,
    position,
    selectedParams,
  }: {
    ampluaId?: number,
    localStorageName: string,
    position: number,
    selectedParams: Array<{}>,
  }) {
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        selectedParams,
      },
    }
    //     localStorage
    saveTablesToLocalStorage({
      id: ampluaId,
      name: localStorageName,
      //@ts-ignore
      tables: self.tables,
    })
  },
}))
